<template>
    <div class="introduce">
        <div class="header">
            <span @click="back" class="back">
                <img src="../../assets/img/back.png">
            </span>
            <span class="headTitle">{{ userType === 'T' ? '教练认证' : '拳馆认证' }}</span>
            <span class="back"></span>
        </div>
        <form class="content">
            <textarea placeholder="请简单介绍下自己(最多100字)" maxlength="100" v-model="introduceMessage"></textarea>
            <button @click="next">下一步</button>
        </form>
    </div>
</template>

<script>
import { uploadParam } from '../../api/getdata'
import { Toast } from 'mint-ui'
import Qs from 'qs'
export default {
  name: 'introduce',
  data () {
    return {
      userType: '',
      introduceMessage: ''
    }
  },
  mounted () {
    this.userType = localStorage.getItem('userType')
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    next () {
      let data = uploadParam(Qs.stringify({
        'introduce': this.introduceMessage
      }))
      data.then(res => {
        console.log(res)
        if (res.status === '0') {
          Toast({
            message: '提交成功',
            duration: 1500
          })
          this.$router.push('/home/index')
        } else {
          Toast({
            message: '提交失败,请稍后重试',
            duration: 1500
          })
        }
      })
    }
  }
}
</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
  }

  .header {
    width: 100%;
    height: 1.5rem;
    display: flex;
    justify-content: space-between;
    font-size: 0.48rem;
  }

  .header span {
    display: flex;
    justify-content: center;
    padding: 0.5rem;
    line-height: 0.5rem;
  }

  .back {
    height: 0.5rem;
    width: 0.266667rem;
  }

  img {
    width: 100%;
    height: 100%;
  }

  .content {
    width: calc(100% - 0.8rem);
    margin: 0 auto;
    margin-top: 2.18667rem;
  }

  textarea {
    width: calc(100% - 0.8rem);
    height: 4.93333rem;
    outline: none;
    border: none;
    resize: none;
    background: #f5f5f5;
    border-radius: 0.2rem;
    padding: 0.4rem;
    font-size: 0.48rem;
  }

  textarea::placeholder {
    color: #c0c0c0;
    font-size: 0.48rem;
  }

  .content button {
    display: block;
    width: 100%;
    background: #ffd200;
    border: none;
    outline: none;
    margin: 1.09333rem auto;
    font-size: 0.426667rem;
    color: #333333;
    height: 1.3333rem;
    border-radius: 0.15rem;
    font-weight: 400;
  }
</style>



